@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Raleway/Raleway-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: url("../../src/fonts/Raleway/Raleway-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 900;
  src: url("../../src/fonts/Raleway/Raleway-Black.ttf") format("truetype");
}
@font-face {
  font-family: "Staatliches";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Staatliches/Staatliches-Regular.ttf") format("truetype");
}
.buscar, .categorias {
  padding-block: 2.4rem;
}
@media (min-width: 768px) {
  .buscar, .categorias {
    padding-block: 3.2rem;
  }
}
.buscar__titulo, .categorias__titulo {
  margin-bottom: 2.4rem;
  text-align: center;
  color: #484349;
}
@media (min-width: 768px) {
  .buscar__titulo, .categorias__titulo {
    font-size: 3.2rem;
  }
}
.buscar__formulario, .categorias__grid {
  display: grid;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .buscar__formulario, .categorias__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.buscar__label {
  overflow: hidden;
  width: 1px;
  height: 1px;
  display: block;
}
.buscar__select, .buscar__input {
  width: 100%;
  height: 100%;
  padding: 0.8rem;
  border: 1px solid #484349;
}
.buscar__boton {
  width: 100%;
  padding: 0.8rem 3.2rem;
  border: none;
  display: inline-block;
  text-align: center;
  font-weight: 700;
  color: #FFFFFF;
  background-color: #B8336A;
  cursor: pointer;
}
@media (min-width: 768px) {
  .buscar__boton {
    width: auto;
  }
  .buscar__boton:hover {
    background-color: #902853;
  }
}
@media (min-width: 768px) {
  .buscar__boton {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 768px) {
  .categorias__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .categorias__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.categorias__img {
  width: 100%;
}
.categorias__nombre {
  margin-top: 0.8rem;
  text-align: center;
  font-weight: 700;
}